body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #D9DBC5;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
}
.snek {
  position: relative;
  margin: 0 auto;
  width: 57vh;
  height: 100%;
}
.display {
  width: 100%;
  height: 70vh;
}
.display__edging {
  width: 47vh;
  margin: 1.25vh auto 0;
  padding: 0 1vh 1vh;
  border: .5vh solid #911;
}
.display__legend {
margin: 0 auto;
font-family: impact;
height: 4vh;
width: 24vh;
min-height: 16px;
min-width: 96px;
background-size: 100% 100%;
background-image: url(/rect-snek/static/media/legend.9a918d49.svg);
border-left: 1.25vh solid #D9DBC5;
border-right: 1.25vh solid #D9DBC5;
}
.display__borders {
  width: 42vh;
  height: 58vh;
  background-color: #909D83;
  border-width: 2.5vh;
  border-style: solid;
  border-top-color: #d6d8c3;
  border-left-color: #bdbfae;
  border-bottom-color: #caccb7;
  border-right-color: #e3e5ce;
}
.display__glass {
  width: 100%;
  height: 100%;
  border-radius: .5vh;
  box-shadow: inset .25vh .25vh 1vh 0 #313729, 1vh 0 1vh 0 #f8f9e3, -1vh 0 1vh 0 #97998c;
}
.display__frame {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  float: left;
  width: 27.5vh;
  height: 55vh;
  margin: 4% 0 4% 3%;
  outline: .5vh solid #313729;
  padding: 0 .5vh;
}
.controls {
  width: 100%;
  height: 28.75vh;
}
